@import '../abstract/variables';
@import '../abstract/mixins';

.navigation {

    &__checkbox {
        display: none;
    }

    &__button {
        background-color: $color-white;
        height: 7rem;
        width: 7rem;
        border-radius: 50%;
        position: fixed;
        top: 6rem;
        right: 6rem;
        z-index: 1010;
        box-shadow: 0 1rem 3rem rgba($color-black, .1);
        text-align: center;
        cursor: pointer;

        @include respond (tab-port) {
            top: 3rem;
        }
        @include respond (phone) {
            top: 3.5rem;
        }
    }

    &__background {
        height: 6.3rem;
        width: 6.3rem;
        border-radius: 50%;
        position: fixed;
        top: 6.3rem;
        right: 6.3rem;
        background-image: radial-gradient($color-primary-light, $color-primary-dark); // gradient goes from center to out in all directions
        z-index: 1000;
        transition: all .6s cubic-bezier(0.83, 0, 0.17, 1); // easeOutQuart
        @include respond (tab-port) {
            top: 3.5rem;
        }
        @include respond (phone) {
            top: 4rem;
        }

        // transform: scale(100); // make the background grow to cover the page
    }

    &__nav {
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 1005;
        
        opacity: 0;
        width: 0; // completely hides the links
        transition: all .5s cubic-bezier(0.68, -0.6, 0.32, 1.6);
    }

    &__list {
        @include center;
        text-align: center;
        list-style: none;
        width: 100%;
    }

    &__item {
        margin: 1rem;
    }
    
    &__link {
        // normal state
        &:link,
        &:visited {
            display: inline-block;
            font-size: 3rem;
            font-weight: 300;
            padding: 1rem 2rem;
            color: $color-white;
            text-decoration: none;
            text-transform: uppercase;
            background-image: linear-gradient(120deg, transparent 0%, transparent 50%, $color-white 50%);
            background-size: 225%;
            transition: all .4s;

            span {
                margin-right: 1.5rem;
                display: inline-block;
            }
        }

        // hover and active state
        &:hover,
        &:active {
            background-position: 100%;
            color: $color-primary;
            transform: translateX(1rem);
        }

    }

    // FUNCTIONALITY

    &__checkbox:checked ~ &__background {
        transform: scale(100);
    }

    &__checkbox:checked ~ &__nav {
        opacity: 1;
        width: 100%;
    }

    // ICON
    &__icon {
        position: relative;
        margin-top: 3.5rem;

        &,
        &:before,
        &:after {
            width: 3rem;
            height: 2px;
            background-color: $color-footer;
            display: inline-block;
        }
        
        &:before,
        &:after {
            content: ""; // so that the before and after elements to be shown
            position: absolute;
            left: 0;
            transition: all .2s;
        }

        &:before { top: -.8rem; }
        &:after { top: .8rem; }
    }

    &__button:hover &__icon::before {
        top: -1rem;
    }

    &__button:hover &__icon::after {
        top: 1rem;
    }

    &__checkbox:checked + &__button &__icon {
        background-color: transparent;
    }

    &__checkbox:checked + &__button &__icon::before {
        top: 0;
        transform: rotate(135deg);
    }

    &__checkbox:checked + &__button &__icon::after {
        top: 0;
        transform: rotate(-135deg);
    }
}