@import '../abstract/variables';
@import '../abstract/mixins';

.feature-box {
    background-color: rgba($color-white, .8);
    font-size: 1.5rem;
    padding: 2.5rem;
    text-align: center; // center the icon font
    border-radius: 3px;
    box-shadow: 0 1.5rem 4rem rgba($color-black, .15);
    transition: transform .3s;

    @include respond (columns-break) {
        padding: 2rem;
    }

    &__icon {
        font-size: 6rem;
        margin-bottom: .5rem;
        display: inline-block; // so gradient fills only the text and not the window
        background-image: linear-gradient(to right, $color-primary-light, $color-primary-dark);
        background-clip: text;
        color: transparent;
        @include respond (columns-break) {
            margin-bottom: 0;
        }
    }

    &:hover {
        transform: translateY(-2rem) scale(1.13);
    }
}